/*
* localstorage.js
* localstorage的實現
*/
// 這個有點奇怪,檔名稱叫local.js不能按照js檔案解析
export default {
    getItem(key){
    let item = localStorage.getItem(key)
    // 這點要判斷是字串還是物件
    let result = /^[{\[].*[}\]]$/g.test(item)
    if (result) {
    return JSON.parse(item)
    } else {
    return item
    }
    },
    setItem(key, value){
    // 這點要判斷是字串還是物件
    if (typeof value == "string") {
    localStorage.setItem(key, value)
    } else {
    let item = JSON.stringify(value)
    localStorage.setItem(key, item)
    }
    },
    removeItem(key){
    localStorage.removeItem(key)
    },
    getAll(){},
    clear(){
    localStorage.clear()
    },
    key(n){},
    forEach(cb){},
    has(key){},
    deleteAllExpires(){},
    // 獲取localstorage最大儲存容量
    getMaxSpace(){
    if (!window.localStorage) {
    console.log('當前瀏覽器不支援localStorage!')
    }
    var test = '0123456789'
    var add = function (num) {
    num  = num
    if (num.length == 10240) {
    test = num
    return
    }
    add(num)
    }
    add(test)
    var sum = test
    var show = setInterval(function () {
    sum  = test
    try {
    window.localStorage.removeItem('test')
    window.localStorage.setItem('test', sum)
    console.log(sum.length / 1024 + 'KB')
    } catch (e) {
    console.log(sum.length / 1024 + 'KB超出最大限制')
    clearInterval(show)
    }
    }, 0.1)
    },
    // 獲取使用了的localstorage的空間
    getUsedSpace(){
    if (!window.localStorage) {
    console.log('瀏覽器不支援localStorage')
    }
    var size = 0
    for (item in window.localStorage) {
    if (window.localStorage.hasOwnProperty(item)) {
    size  = window.localStorage.getItem(item).length
    }
    }
    console.log('當前localStorage使用容量為' + (size / 1024).toFixed(2) + 'KB')
    }
    }