




































  // TypeScript
import $ from 'jquery';
import storage from '../utils/storage.js';

import { Component, Prop, Vue } from 'vue-property-decorator';

enum eBtn {
  none = -1,
  load = 0,
  add = 1,
  delete = 2,
}

@Component
export default class OptionalStock extends Vue {
  @Prop() private msg!: any;

  private functionType: number = eBtn.none;
  private currentIndex: number = -1;  // 自選股 的 index
  private optionalStock0: any[] = [];
  private optionalStock1: any[] = [];
  private optionalStock2: any[] = [];
  private optionalStock3: any[] = [];
  private optionalStock4: any[] = [];

  private title: string = '自選股';
  private showOptionalMenu: boolean = false;

  private toastTitle: string = "";
  private toastMsg: string = "";
  // methods:
  // --- 預設值 (展示用) ---
  private initOptionalStock() {
    this.optionalStock0 = storage.getItem('OptionalStock0');
    // console.log("this.optionalStock0", this.optionalStock0);
    if (this.optionalStock0 === null) {
      storage.setItem('OptionalStock0', [{name: "0050", value: "0050"},
      {name: "0056", value: "0056"}, {name: "2915", value: "2915"}]);
    }

    // 市值最高
    this.optionalStock1 = storage.getItem('OptionalStock1');
    // console.log("this.optionalStock1", this.optionalStock1);
    if (this.optionalStock1 === null) {
      storage.setItem('OptionalStock1', [{name: "2330", value: "2330"},
      {name: "2317", value: "2317"}, {name: "2454", value: "2454"},
      {name: "2412", value: "2412"}, {name: "6505", value: "6505"}]);
    }

    // 0056 不死鳥
    this.optionalStock2 = storage.getItem('OptionalStock2');
    // console.log("this.optionalStock2", this.optionalStock2);
    if (this.optionalStock2 === null) {
      storage.setItem('OptionalStock2', [{name: "2301", value: "2301"},
      {name: "2886", value: "2886"}, {name: "3034", value: "3034"},
      {name: "2324", value: "2324"}, {name: "1101", value: "1101"},
      {name: "2356", value: "2356"}, {name: "2382", value: "2382"},
      {name: "2385", value: "2385"}, {name: "3231", value: "3231"},
      {name: "3702", value: "3702"}]);
    }

    // ETF
    this.optionalStock3 = storage.getItem('OptionalStock3');
    // console.log("this.optionalStock3", this.optionalStock3);
    if (this.optionalStock3 === null) {
      storage.setItem('OptionalStock3', [{name: "0050", value: "0050"},
      {name: "0056", value: "0056"}, {name: "00861", value: "00861"},
      {name: "006208", value: "006208"}, {name: "0051", value: "0051"},
      {name: "00639", value: "00639"}]);
    }

    // 推薦
    this.optionalStock4 = storage.getItem('OptionalStock4');
    // console.log("this.optionalStock4", this.optionalStock4);
    if (this.optionalStock4 === null) {
      storage.setItem('OptionalStock4', [{name: "2912", value: "2912"},
      {name: "5871", value: "5871"}, {name: "2884", value: "2884"},
      {name: "2303", value: "2303"}, {name: "2330", value: "2330"}]);
    }
  }

  // 顯示選單
  private toggleOptionalStock(btnType) {
    this.showOptionalMenu = !this.showOptionalMenu;

    if (this.showOptionalMenu) {
      this.functionType = btnType;
    } else {
      this.functionType = eBtn.none;
    }
  }

  private selectOptionalStock(index) {
    if (this.showOptionalMenu) {
      switch (this.functionType) {
          case eBtn.load :
            this.loadOptionalStock(index);
            break;
          case eBtn.add :
            this.addOptionalStock(index);
            break;
          case eBtn.delete :
            this.deleteOptionalStock(index);
            break;
          default : /* 可選的 */
            break;
      }
    }

    this.showOptionalMenu = !this.showOptionalMenu;
  }

  private loadOptionalStock(index) {
    this.currentIndex = index;
    this.$emit('emitLoaded', index);
    this.title = "自選股" + (index + 1);
  }

  private addOptionalStock(index) {
    this.currentIndex = index;
    let doItFlag: number = 0;
    let selectedArray: any[] = [];
    selectedArray = storage.getItem('OptionalStock_selected');

    if (this.currentIndex === -1) {
      alert("請選擇一項自選股");
    } else {
      let currentArray: any[] = [];
      currentArray = storage.getItem('OptionalStock' + index);
      // alert("this.currentIndex="+this.currentIndex);
      selectedArray.forEach( (element) => {
        console.log("element =", element) ;
        // 檢查是否在裡面
        let inArray = 0;
        for (const cItem of currentArray) {
          console.log("cItem =", cItem) ;
          const key = "value";
          if (cItem[key] === element[key]) {
            inArray = 1;
            break;
          }
        }

        if (inArray === 0) {
          doItFlag = 1;
          currentArray.push(element);
        }
      });
      // console.log("currentArray =", currentArray) ;
      storage.setItem('OptionalStock' + index, currentArray);

      if (doItFlag === 1) {
        this.toastTitle = "加入";
        this.toastMsg = "股票已經加入自選股" + (this.currentIndex + 1);
        $('.toast').toast('show');
        this.loadOptionalStock(index);
      }
    }
  }

  private deleteOptionalStock(index) {
    this.currentIndex = index;
    let doItFlag: number = 0;
    let selectedArray: any[] = [];
    selectedArray = storage.getItem('OptionalStock_selected');

    if (this.currentIndex === -1) {
      alert("請選擇一項自選股");
    } else {
      let currentArray: any[] = [];
      currentArray = storage.getItem('OptionalStock' + index);
      // alert("this.currentIndex="+this.currentIndex);
      selectedArray.forEach( (element) => {
        console.log("element =", element) ;
        // 檢查是否在裡面
        let inArray = 0;
        for (let iCnt = 0; iCnt < currentArray.length; iCnt++) {
          const cItem = currentArray[iCnt];
          console.log("cItem =", cItem) ;
          const key = "value";
          if (cItem[key] === element[key]) {
            inArray = 1;
            currentArray.splice(iCnt, 1);
            doItFlag = 1;
            break;
          }
        }
      });
      // console.log("currentArray =", currentArray) ;
      storage.setItem('OptionalStock' + index, currentArray);

      if (doItFlag === 1) {
        this.toastTitle = "刪除";
        this.toastMsg = "股票已從自選股" + (this.currentIndex + 1) + "刪除";
        $('.toast').toast('show');
        this.loadOptionalStock(index);
      }
    }
  }

  // === === 生命週期也是直接定義同名屬性 === ===
  private mounted() {
    this.initOptionalStock();
    $('[data-toggle="tooltip"]').tooltip();
    $('.toast').toast('dispose');
  }
}
